export default {
	data() {
		return {
			animationData: {},
			animation: {}
		}
	},
	onShow: function() {
		var animation = uni.createAnimation({
			transformOrigin: "50% 50%",
			duration: 1000,
			timingFunction: "ease",
			delay: 0
		})
		this.animation = animation
		this.animation.scale(2,2).step()
		this.animation.backgroundColor('#000').opacity('0.2').step()
		this.animation.backgroundColor('#fff').opacity('1').step()
		this.animation.translate(100).step({
			duration: 1000
		})
		this.animationData = this.animation.export()
	},
	onLoad() {

	},
	methods: {

	}
}
